<template>
  <div class="container">
    <div class="row clearfix">
      <div class="col-lg-12">
        <div class="card chat-app">
          <ChatUsers @changed="toUserNameFilled" />
          <div class="chat">
            <div class="chat-header clearfix">
              <div class="row">
                <div class="col-lg-6">
                  <a
                    href="javascript:void(0);"
                    data-toggle="modal"
                    data-target="#view_info"
                  >
                    <img
                      src="@/assets/avatar.png"
                      alt="avatar"
                    />
                  </a>
                  <div v-if="toUserName != null" class="chat-about">
                    <h6 class="m-b-0">{{ toUserName }}</h6>
                    <small style="display:none">Last seen: 2 hours ago</small>
                  </div>
                </div>
              </div>
            </div>
            <ChatList :chatList="chatList" :toUsername="toUsername" />
            <div class="chat-history">
              <ul class="m-b-0">
                

                <li class="clearfix">
                  <div class="message-data text-right">
                    <span class="message-data-time">10:10 AM, Today</span>
                    <img
                      src="@/assets/avatar.png"
                      alt="avatar"
                    />
                  </div>
                  <div class="message other-message float-right">
                    Hi Aiden, how are you? How is the project coming along?
                  </div>
                </li>
                <li class="clearfix">
                  <div class="message-data">
                    <span class="message-data-time">10:12 AM, Today</span>
                  </div>
                  <div class="message my-message">Are we meeting today?</div>
                </li>
                <li class="clearfix">
                  <div class="message-data">
                    <span class="message-data-time">10:15 AM, Today</span>
                  </div>
                  <div class="message my-message">
                    Project has been already finished and I have results to show
                    you.
                  </div>
                </li>
              </ul>
            </div>
            <div class="chat-message clearfix">
              <div class="input-group mb-0">
                <div class="input-group-prepend">
                  <span v-on:click="sendMessage" class="input-group-text"
                    ><i class="fa fa-send"></i
                  ></span>
                </div>
                <input
                  v-model="newMessage"
                  type="text"
                  class="form-control"
                  placeholder="Enter text here..."
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import ChatUsers from "./ChatUsers";
import ChatList from "./ChatList";
import authHeader from "../service/DataService";
import URLS from "../constants/urls";

export default {
  name: "Chat",
  components: { ChatUsers, ChatList },
  data() {
    return {
      toUserName: null,
      chatList: [],
      newMessage: "",
    };
  },
  methods: {
    toUserNameFilled(value) {
      this.toUserName = value;
      console.log(value);
      this.loadChatList();
    },
    loadChatList() {
      axios
        .get(URLS.API_CHAT + "?withUsername=" + this.toUserName, {
          headers: authHeader(),
        })
        .then((res) => {
          if (res.status === 200) {
            this.chatList = res.data;
          }
        });
    },
    sendMessage() {
      if (this.toUserName && this.newMessage) {
        const postData = {
          toUserName: this.toUserName,
          message: this.newMessage,
        };
        axios
          .post(URLS.API_CHAT, postData, {
            headers: authHeader(),
          })
          .then((res) => {
            if (res.status === 200) {
              console.log("sended");
              this.newMessage = "";
              this.loadChatList();
            }
          });
      }
    },
  },
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



